.form{
    margin-top: 10px;
    height: 180px;
    width: 400px;
    text-align: left;
    border: 1px solid #CCCCCC;
    margin-left: 20%;
    border-radius: 5px;
    background-color: #fff;
    margin:0 auto;
    margin-top: 10px;
}

.avatar {
    margin: 0px 5px;
    vertical-align: top;
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }

.title{
    width: 100%;
    height: 30%;
    background-color: #F2F3F5;
    border-bottom: 1px solid gainsboro;
    
}

textarea {
    width: 80%;
    border: 0ch;
    height: 50px;
    margin-left: 10px;
  }
label {
    width: 100%;
    margin-left: 20px;
    line-height: 40px;
}

.btn {
    display: inline-block;
    margin-bottom: 0;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    border-radius: 4px;
    margin: 10px;
}